@keyframes loading {
  0% {
    top: 0; }
  30% {
    top: -8px; }
  60% {
    top: 0; } }

.bubble__loader {
  position: absolute;
  top: 26px;
  left: 80px;
  display: block;
  transition: visibility 0.3s, opacity 0.3s;
  opacity: 0;
  visibility: hidden; }
  .bubble__loader span {
    position: relative;
    display: block;
    width: 8px;
    height: 8px;
    background-color: #ffffff;
    border-radius: 50%;
    animation-delay: 0.25s;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-name: loading;
    animation-timing-function: ease-out; }
  .bubble__loader::before {
    position: absolute;
    left: -16px;
    width: 8px;
    height: 8px;
    content: "";
    background-color: #ffffff;
    border-radius: 50%;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-name: loading;
    animation-timing-function: ease-out; }
  .bubble__loader::after {
    position: absolute;
    top: 0;
    left: 16px;
    width: 8px;
    height: 8px;
    margin-right: 10px;
    content: "";
    background-color: #ffffff;
    border-radius: 50%;
    animation-delay: 0.5s;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-name: loading;
    animation-timing-function: ease-out; }

